header{
    height: 100vh;
padding-top: 7rem;
overflow: hidden;

}
.header__container{
    text-align: center;
    height:100%;
    position: relative;
}
.cta {
    margin-top: 2 5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    padding-top: 2%;
}
.header__Socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    position: absolute;
    left: 0%;
    bottom:16rem;
   font-size: xx-large ;
} 

.P{
    background: var(--color-primary);
    width: 22rem;
    height: 30 rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 4rem;
border-radius: 12rem 12rem 5rem 5rem;
overflow: hidden;
padding: orem;
}
 .scroll-down{
    position: absolute;
    right: 2.3rem;
    bottom: 8rem;
    transform:rotate(90deg) ;
    font-weight: 300;
    font-size: 1rem;
 }
 @media screen and (max-width:1024px)  {
     header {
        height: 68vh;
     }
     
 }
 @media screen and (max-width:600px)  {
    header {
        height: 100vh;
      
     }
     .header__Socials,
     .scroll-down{
        display: none;
     }
 }